/* @flow */

import { warn } from '@pspdfkit/shared'
import type { FormFieldWithWidgets } from '../models/FormFieldWithWidgets'
import type { BaseFormFieldJSON } from '../lib/Backend'

export type ActionOptionsType = {
  skippedPdfObjectIds: Array<number>,
  annotations: Array<{ pdfObjectId?: number, imageAttachmentId: string, id: number }>,
  formFieldValues: Array<{}>,
  formFields: Array<BaseFormFieldJSON & { value: any }>,
  attachments: { [string]: Uint8Array },
  skippedPdfBookmarkIds?: Array<string>,
  skippedPdfFormFieldIds?: Array<number>,
  bookmarks?: Array<string>,
}

// Add flag to prevent restoring state when applying operations.
export function performActionWithStateGeneric<T>(
  {
    skippedPdfObjectIds,
    annotations,
    formFieldValues,
    formFields,
    skippedPdfFormFieldIds = [],
    attachments,
    skippedPdfBookmarkIds = [],
    bookmarks = [],
  }: ActionOptionsType,
  action: () => T,
  readFormJSONObjects: () => Array<FormFieldWithWidgets>,
  applyFormJSONObjects: (formFieldsWithWidgets: Array<FormFieldWithWidgets>) => Array<Object>,
  deleteAnnotation: (id: number) => mixed,
  updateAnnotation: (annotation: {}) => number,
  createAnnotation: (annotation: {}, attachment: Uint8Array) => number,
  removeBookmarks: (bookmarkIds: Array<string>) => mixed,
  appendBookmarks: (bookmarks: Array<string>) => mixed,
  setFormValues: (formFieldValues: Array<{}>) => mixed,
  restoreToOriginalState: () => mixed,
  cleanup?: boolean = true,
): T {
  // We only need to restore the PDF if we make any mutating change. In some cases (like the
  // rendering of tiles) this is never the case.
  let didMutatePdf = false
  try {
    // We need the original data from readFormJSONObjects() later to look for old widgets
    // that may correspond to new or updated form fields, so we keep it in documentFormJSON
    // and make a shallow clone in formJSONToApply that we'll filter of skipped form fields
    // and populate with new and updated form fields (which come in `formFields`).
    const documentFormJSON = readFormJSONObjects()
    let formJSONToApply = [...documentFormJSON]

    const updatedObjectIds = annotations.map(annotation => annotation.pdfObjectId).filter(Boolean)

    didMutatePdf = skippedPdfObjectIds.length > 0 || skippedPdfFormFieldIds.length > 0
    for (let id of skippedPdfObjectIds) {
      if (!updatedObjectIds.includes(id)) {
        deleteAnnotation(id)
      }
    }

    // Filter out all form fields present in skippedPdfFormFieldIds, wether updated or deleted,
    // as we'll have to rebuild the updated form fields anyway.
    if (skippedPdfFormFieldIds) {
      formJSONToApply = formJSONToApply.filter(
        formFieldWithWidgets =>
          !skippedPdfFormFieldIds.includes(formFieldWithWidgets.formField.pdfObjectId),
      )
    }

    // Build the final formFieldWithWidgets array to be passed to applyFormJSONObjects()
    formJSONToApply = formJSONToApply.concat(
      formFields.map(formField => ({
        formField,
        value: formField.value,
        type: 'pspdfkit/form-field-with-widgets',
        // New or updated form fields widget annotations are either new (and therefore present in
        // `annotations`) or present in the original document (and therefore available in `documentFormJSON`)
        widgets: formField.annotationIds
          .map(id => {
            // Look for the widget in the new and updated annotations list
            let annotation = annotations.find(annotation => annotation.id === id)
            if (!annotation) {
              // If it's not there, it hasn't been created nor updated; look for it in the document's
              // original widget list
              documentFormJSON.some(formFieldWithWidgets => {
                annotation = formFieldWithWidgets.widgets.find(
                  annotation => annotation.pdfObjectId === id,
                )
                return annotation
              })
            }
            return annotation
          })
          // All the widget annotation's should be either in the original form JSON data or
          // in the updated / created annotations list, so it should never be `null`, but it may
          // happen if a new widget is deleted and the associated form field is not updated before
          // performActionWithState() is called. In order to avoid failing in these cases, we filter
          // any possible `null` widget.
          .filter(Boolean),
      })),
    )

    didMutatePdf = annotations.length > 0
    for (let annotation of annotations) {
      const shouldUpdate = updatedObjectIds.includes(annotation.pdfObjectId)
      try {
        if (shouldUpdate) {
          updateAnnotation(annotation)
        } else {
          createAnnotation(annotation, attachments[annotation.imageAttachmentId])
        }
      } catch (error) {
        warn(`An error ocurred while ${shouldUpdate ? 'updating' : 'creating'} annotation #${
          annotation.id
        }. The annotation was skipped.

${error.message}`)
      }
    }

    if (formFields.length > 0 || skippedPdfFormFieldIds.length > 0) {
      didMutatePdf = true
      applyFormJSONObjects(formJSONToApply)
    }

    if (skippedPdfBookmarkIds.length > 0) {
      didMutatePdf = true
      removeBookmarks(skippedPdfBookmarkIds)
    }

    if (bookmarks.length > 0) {
      didMutatePdf = true
      appendBookmarks(bookmarks)
    }

    if (formFieldValues.length > 0) {
      didMutatePdf = true
      setFormValues(formFieldValues)
    }

    return action()
  } finally {
    if (didMutatePdf && cleanup) {
      restoreToOriginalState()
    }
  }
}
