/* @flow */

import PSPDFKitError from './PSPDFKitError'

/**
 * A runtime assertion helper. If `condition` is not truthy, the execution will
 * stop and an error will be raised.
 *
 * Naming this function `invariant` is important since it can be used to give
 * Flow special hints (that `condition` must be truthy).
 *
 * For example:
 *
 *   let a: ?string = notSureIfStringOrNot();
 *   invariant(a, 'a must not be null');
 *   a.replace('foo', 'bar');
 */
export default function invariant(condition: any, message?: string): void {
  if (!condition) {
    throw new PSPDFKitError(`Assertion failed: ${message || 'Condition not met'}

For further assistance, please go to: https://pspdfkit.com/support/request`)
  }
}
