  async applyOperations(
    operations: Array<DocumentOperationJSON>,
    operationsDocuments: { [string]: Blob },
    skippedPdfObjectIds: Array<number>,
    annotations: Array<Object>,
    formFieldValues: Array<Object>,
    attachments: { [string]: Blob },
    bookmarks: Array<Object>,
    skippedPdfBookmarkIds: Array<string>,
    formFields: Array<Object>,
    skippedPdfFormFieldIds: Array<number>,
  ): Promise<Array<Object>> {
    // `operations` may be mutated in saveOperationsDocuments()
    const deleteOperationsDocuments = await saveOperationsDocuments(operations, operationsDocuments)

    const result = applyOperations(
      operations,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      await attachmentBlobsToUint8Arrays(attachments),
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
      CREATE_FILE_PATH,
    )

    // Invalidate document form fields and widgets cache
    formJSONObjects = null

    deleteOperationsDocuments()

    return result
  }

  async exportPDFWithOperations(
    operations: Array<DocumentOperationJSON>,
    operationsDocuments: { [string]: Blob },
    skippedPdfObjectIds: Array<number>,
    annotations: Array<Object>,
    formFieldValues: Array<Object>,
    attachments: { [string]: Blob },
    bookmarks: Array<Object>,
    skippedPdfBookmarkIds: Array<string>,
    formFields: Array<Object>,
    skippedPdfFormFieldIds: Array<number>,
  ): Promise<ArrayBuffer> {
    // `operations` may be mutated in saveOperationsDocuments()
    const deleteOperationsDocuments = await saveOperationsDocuments(operations, operationsDocuments)

    const result = exportPDFWithOperations(
      operations,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      await attachmentBlobsToUint8Arrays(attachments),
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
    )

    deleteOperationsDocuments()

    return result
  }

