  async renderTile(
    pageIndex: number,
    pageSize: {| width: number, height: number |},
    rect: {| left: number, top: number, width: number, height: number |},
    renderForPrinting: boolean,
    annotations: ?Array<Object>,
    formFieldValues: ?Array<Object>,
    attachments: ?{ [string]: Blob },
    formFields: ?Array<Object>,
  ): Promise<ArrayBuffer> {
    let skippedPdfObjectIds = []
    let skippedPdfFormFieldIds = []

    // Collect the document annotations so that performActionWithState can remove them
    // before adding (or effectively update) the annotations passed to renderTile.
    if (annotations) {
      skippedPdfObjectIds = exec('annotationsForPageIndex', pageIndex).annotations.map(
        annotation => annotation.pdfObjectId,
      )
    }

    // Collect the document form fields so that performActionWithState can remove them
    // before adding (or effectively update) the form fields passed to renderTile.
    if (formFields) {
      skippedPdfFormFieldIds = readFormJSONObjects().map(
        formFieldWithWidgets => formFieldWithWidgets.formField.pdfObjectId,
      )
    }

    return performActionWithState(
      {
        skippedPdfObjectIds,
        annotations: annotations || [],
        formFieldValues: formFieldValues || [],
        attachments: await attachmentBlobsToUint8Arrays(attachments),
        formFields: formFields || [],
        skippedPdfFormFieldIds,
      },
      () => renderTile(pageIndex, pageSize, rect, renderForPrinting, annotations, formFieldValues),
    )
  }

  async renderAnnotation(
    annotation: Object,
    attachment: ?Blob,
    width: number,
    height: number,
  ): Promise<ArrayBuffer> {
    if (attachment) {
      return readBlobData(attachment).then(attachmentData =>
        renderAnnotation(annotation, attachmentData, width, height),
      )
    } else {
      return renderAnnotation(annotation, attachment, width, height)
    }
  }

  async renderDetachedAnnotation(
    annotation: Object,
    attachment: ?Blob,
    width: number,
    height: number,
  ): Promise<ArrayBuffer> {
    if (attachment) {
      const attachmentType = attachment.type
      return readBlobData(attachment).then(attachmentData =>
        renderDetachedAnnotation(annotation, attachmentData, width, height, attachmentType),
      )
    } else {
      return renderDetachedAnnotation(annotation, null, width, height, null)
    }
  }

