// @flow

export function getIndexedDB(scope: Object = window): IDBFactory {
  return (
    scope.indexedDB ||
    scope.webkitIndexedDB ||
    scope.mozIndexedDB ||
    scope.OIndexedDB ||
    scope.msIndexedDB
  )
}

export function isIndexedDBSupported(indexedDB: IDBFactory) {
  return typeof indexedDB !== 'undefined'
}

type WASMCacheConfig = {
  name: string,
  storeName: string,
}

type WASMCache = {
  setItem: (key: string, value: any) => Promise<void>,
  getItem: (key: string) => Promise<any>,
  clear: () => Promise<void>,
}

export function getCache(
  indexedDB: IDBFactory,
  { name, storeName }: WASMCacheConfig,
): Promise<WASMCache> {
  return new Promise((resolve, reject) => {
    const version = 1
    const request = indexedDB.open(name, version)

    request.onsuccess = event => {
      const db = event.target.result

      if (db.setVersion && db.version !== version) {
        const setVersion = db.setVersion(version)
        setVersion.onsuccess = function() {
          db.createObjectStore(name)
        }
      }

      // Resolve with `{ setItem, getItem }`
      resolve({
        setItem: (key, value) =>
          new Promise((resolve, reject) => {
            try {
              const result = db
                .transaction([name], 'readwrite')
                .objectStore(storeName)
                .put(value, key)

              result.onsuccess = resolve
              result.onerror = reject
            } catch (e) {
              reject()
            }
          }),

        /*
         * Returns a Promise that resolves with the `value` for `key`.
         */
        getItem: key =>
          new Promise((resolve, reject) => {
            try {
              const result = db
                .transaction([name])
                .objectStore(storeName)
                .get(key)

              result.onsuccess = event => {
                resolve(event.target.result)
              }
              result.onerror = reject
            } catch (e) {
              reject()
            }
          }),

        clear: () =>
          new Promise((resolve, reject) => {
            try {
              const result = db
                .transaction([name], 'readwrite')
                .objectStore(storeName)
                .clear()
              result.onsuccess = resolve
              result.onerror = reject
            } catch (e) {
              reject()
            }
          }),
      })
    }

    // This will open/create the database.
    request.onupgradeneeded = event => {
      const db = event.target.result
      db.createObjectStore(name)
    }

    request.onerror = reject
  })
}
