/* @flow */

/**
 * @classdesc
 * A PSPDFKit.Error indicates a problem with PSPDFKit. It is a subclass of and behaves like a
 * regular JavaScript error.
 *
 * @example
 * try {
 *   PSPDFKit.someFunctionThatFails();
 * } catch (error) {
 *   (error instanceof PSPDFKit.Error); // => true
 *   error.message; // Useful error message
 * }
 * @public
 * @memberof PSPDFKit
 * @class Error
 * @extends Error
 * @summary PSPDFKit related error.
 */
function PSPDFKitError(messageOrError: string | Error) {
  let error
  if (messageOrError instanceof Error) {
    error = messageOrError
  } else {
    error = new Error(messageOrError)
  }

  Object.setPrototypeOf(error, PSPDFKitError.prototype)
  return error
}

// Use some prototype magic in order to make `new PSPDFKitError() instanceof PSPDFKitError`
PSPDFKitError.prototype = Object.create(Error.prototype, {
  name: {
    value: 'PSPDFKitError',
    enumerable: false,
  },
})

export default PSPDFKitError
