/* @flow */

// $FlowIssue
import WASM_CHECKSUM from '@pspdfkit/checksum-loader!../../vendor/pspdfkit.wasm'
// $FlowIssue
import WASM_JS_CHECKSUM from '@pspdfkit/checksum-loader!../../vendor/pspdfkit.wasm.js'
// $FlowIssue
import ASM_CHECKSUM from '@pspdfkit/checksum-loader!../../vendor/pspdfkit.asm.js'
// $FlowIssue
import ASM_MEM_CHECKSUM from '@pspdfkit/checksum-loader!../../vendor/pspdfkit.asm.js.mem'

import { isIndexedDBSupported, getCache, getIndexedDB } from '../lib/indexed-db'
import { log, error, warn, invariant } from '@pspdfkit/shared'

const WASM_CACHE_KEY: string = WASM_CHECKSUM + WASM_JS_CHECKSUM
const indexedDB = getIndexedDB(global)

const isBundled = process.env.NODE_ENV !== 'test'

const LIB_DIR = isBundled ? 'pspdfkit-lib/' : ''
const WASM_INIT_FILE = `${LIB_DIR}pspdfkit${isBundled ? '-' + WASM_JS_CHECKSUM : ''}.wasm.js`
const WASM_FILE = `${LIB_DIR}pspdfkit${isBundled ? '-' + WASM_CHECKSUM : ''}.wasm`
const ASM_JS_FILE = `${LIB_DIR}pspdfkit${isBundled ? '-' + ASM_CHECKSUM : ''}.asm.js`
const ASM_JS_MEM_FILE = `${LIB_DIR}pspdfkit${isBundled ? '-' + ASM_MEM_CHECKSUM : ''}.asm.js.mem`

/**
 * Loads and initializes a native module
 *
 * We need to wrap the resolved module into a new object since the native module defines a `then`
 * property which would otherwise never resolve.
 */
export function loadNativeModule(
  baseUrl: string,
  mainThreadOrigin: string,
  disableWebAssembly: boolean,
  disableWebAssemblyStreaming: boolean,
  disableIndexedDBCaching: boolean,
  global: window = self,
): Promise<{| nativeModule: Object |}> {
  return new Promise(async resolve => {
    let initialModule = initializeModule(
      baseUrl,
      disableWebAssemblyStreaming,
      disableIndexedDBCaching,
    )
    const useWASM = disableWebAssembly ? false : isWASMSupported(global)
    const factory = useWASM
      ? await isomorphicRequire(`${baseUrl}${WASM_INIT_FILE}`)
      : await isomorphicRequire(`${baseUrl}${ASM_JS_FILE}`)

    log(`Using ${useWASM ? 'WASM' : 'ASM.js'} method`)

    injectRequiredGlobals(global, mainThreadOrigin)

    // In a browser environment, initializing the module is asynchronous and thus we have to wait
    // for the _Main method in the initialized module to be called
    const nativeModule = factory({
      ...initialModule,
      onRuntimeInitialized: () => {
        resolve({ nativeModule })
      },
    })

    // Emscripten removed support for simulating the random device using the insecure Math.random().
    // However, the crypto API is not available in web workers on IE11 and Edge which resulted in
    // issues with a missing random_device implementation.
    //
    // The original Emscripten implementation now handles the case of an implemented crypto library
    // or otherwise falls back to the node module. We, however, don't compile the node library which
    // is why we only have to check for crypto (and have to shim this API when we mount it in Node).
    //
    // https://github.com/PSPDFKit/PSPDFKit-Web/issues/2802
    // https://github.com/emscripten-core/emscripten/pull/7096
    const hasCrypto = typeof global.crypto !== 'undefined'
    if (!hasCrypto) {
      if (process.env.NODE_ENV === 'development') {
        log('Bad random_device enabled: https://github.com/PSPDFKit/PSPDFKit-Web/issues/2802')
      }
      const { FS } = nativeModule
      const badRandomDevice = function() {
        return (Math.random() * 256) | 0
      }
      FS.unlink('/dev/random')
      FS.unlink('/dev/urandom')
      FS.createDevice('/dev', 'random', badRandomDevice)
      FS.createDevice('/dev', 'urandom', badRandomDevice)
    }
  })
}

/**
 * Basic WASM feature test
 */
export function isWASMSupported(global: window) {
  try {
    if (
      // After doing some testing we concluded that it is best to force ASM.js on Safari 11
      // since WASM crashes https://github.com/PSPDFKit/PSPDFKit/pull/17123#issuecomment-428600399
      // For iOS ~11.2.x we know why: https://github.com/kripken/emscripten/issues/6042
      /Version\/11\..*Safari/i.test(global.navigator.userAgent)
    ) {
      return false
    }
  } catch (_) {
    // In case of an error, we simply continue to the regular feature check
  }

  return (
    typeof global.WebAssembly === 'object' && typeof global.WebAssembly.instantiate === 'function'
  )
}

/**
 * The Module is the global injection point for the native WASM/ASM.js libraries.
 *
 * We explicity force the type to `any` since the native modules will also inject
 * their API here and we want to call them
 */
export function initializeModule(
  baseUrl: string,
  disableWebAssemblyStreaming: boolean,
  disableIndexedDBCaching: boolean,
) {
  let module = {
    ENVIRONMENT: process.env.NODE_ENV === 'test' ? 'NODE' : undefined,

    // We manually set the WASM binary file path so we can append baseUrl properly
    wasmBinaryFile: baseUrl + WASM_FILE,

    // Rename the memory initializer filename
    locateFile: (file: string) =>
      file === 'pspdfkit.asm.js.mem'
        ? process.env.NODE_ENV === 'test'
          ? 'vendor/pspdfkit.asm.js.mem'
          : baseUrl + ASM_JS_MEM_FILE
        : file,
  }

  // To include the HTTP authentication headers in a browser environment, we have to patch the
  // binary load methods of Emscripten.
  if (process.env.NODE_ENV !== 'test') {
    // For WASM, we can write a custom instantiate method.
    // https://goo.gl/DNBN7a
    // $FlowIssue
    module['instantiateWasm'] = (imports: any, successCallback: (instance: any) => void) => {
      ;(async () => {
        let cache = null

        if (!disableIndexedDBCaching && isIndexedDBSupported(indexedDB)) {
          try {
            cache = await getCache(indexedDB, {
              name: 'PSPDFKIT_WASM_CACHE',
              storeName: 'PSPDFKIT_WASM_CACHE',
              version: 1,
            })
          } catch (_error) {
            cache = null
          }
        }

        /* eslint-disable */
        log(`Start ${module.wasmBinaryFile} download.`)
        const t1 = Date.now()

        if (cache) {
          const compiledModule = await cache.getItem(WASM_CACHE_KEY)

          if (compiledModule) {
            log(`Cached WASM module found. Reading from cache took: ${Date.now() - t1}ms`)
            const instance = await WebAssembly.instantiate(compiledModule, imports)

            log(`Instantiation complete, took: ${Date.now() - t1}ms`)
            successCallback(instance)
            return
          }
        }

        const fetchPromise = fetch(module.wasmBinaryFile, { credentials: 'same-origin' })

        let instantiatePromise, t2
        const isInstantiateStreamingSupported =
          !disableWebAssemblyStreaming && typeof WebAssembly.instantiateStreaming == 'function'

        function instantiateWithoutStreaming() {
          return fetchPromise
            .then(r => {
              if (!r.ok) {
                throw new Error(`Error loading ${module.wasmBinaryFile}: ${r.statusText}`)
              }
              return r.arrayBuffer()
            })
            .then(wasmBinary => {
              t2 = Date.now()
              log(`Download complete, took: ${t2 - t1}ms`)
              return WebAssembly.instantiate(new Uint8Array(wasmBinary), imports)
            })
        }

        if (isInstantiateStreamingSupported) {
          instantiatePromise = WebAssembly.instantiateStreaming(fetchPromise, imports)
            .then(output => {
              t2 = Date.now()
              log(`Download and Instantiation complete, took: ${Date.now() - t1}ms`)
              return output
            })
            .catch(e => {
              if (/mime.*type/i.test(e.message)) {
                // eslint-disable-next-line no-console
                console.error(e.message)
                return null
              }
              throw e
            })
        } else {
          instantiatePromise = instantiateWithoutStreaming()
        }

        let output = await instantiatePromise

        if (output === null) {
          // eslint-disable-next-line no-console
          console.error(
            'Streaming instantiation failed! ' +
              'Falling back to classic instantiation. ' +
              'This might result in slower initialization time ' +
              'therefore we highly recommend to follow ' +
              'the troubleshooting instructions in our guides to fix this error: https://pspdfkit.com/guides/web/current/pspdfkit-for-web/troubleshooting/#response-has-unsupported-mime-type-error.',
          )
          output = await instantiateWithoutStreaming()
        }

        if (!isInstantiateStreamingSupported && t2) {
          log(`Compilation and Instantiation complete, took: ${Date.now() - t2}ms`)
        }

        if (cache) {
          cache
            .clear()
            .then(() =>
              // $FlowIssue: already checked for this to be defined.
              cache.setItem(WASM_CACHE_KEY, output.module),
            )
            .catch(() => {
              /* Ignore any error. We just don't cache. */
            })
        }

        successCallback(output.instance)
        /* eslint-enable */
      })()
      return {}
    }

    // ASM.js allows the injection of a custom `readAsync` implementation.
    // https://goo.gl/iAL3TK
    // $FlowIssue
    module['readAsync'] = (url, onload, onerror) => {
      fetch(url, { credentials: 'same-origin' })
        .then(r => {
          if (!r.ok) {
            throw new Error(`Error loading ${url}: ${r.statusText}`)
          }
          return r.arrayBuffer()
        })
        .then(buffer => onload(buffer))
        .catch(e => {
          error('ASM.js memory instantiation failed.')
          error(e)
          onerror()
        })
    }
  } else {
    // $FlowIssue
    module['readBinary'] = function() {
      const fs = require('fs')
      const buf = fs.readFileSync('./vendor/pspdfkit.wasm')
      return new Uint8Array(buf)
    }
  }

  return module
}
/**
 * PSPDFCore expects some objects in the global namespace.
 */
export function injectRequiredGlobals(global: window, mainThreadOrigin: string) {
  // We skip info, debug and trace messages when `PSPDFKIT_DEFINES.INTERNAL_DEBUG` is not activated.
  global.PSPDFLoggingServices = {
    error(tag, line) {
      error(`[${tag}] ${line}`)
    },
    warn(tag, line) {
      warn(`[${tag}] ${line}`)
    },
    info(tag, line) {
      if (PSPDFKIT_DEFINES.INTERNAL_DEBUG) {
        log(`[${tag}] ${line}`)
      }
    },
    debug(tag, line) {
      if (PSPDFKIT_DEFINES.INTERNAL_DEBUG) {
        log(`[${tag}] ${line}`)
      }
    },
    trace(tag, line) {
      if (PSPDFKIT_DEFINES.INTERNAL_DEBUG) {
        log(`[${tag}] ${line}`)
      }
    },
  }

  // License related callbacks are obfuscated on purpose
  global.BEEE5011DDCD4E47A799CDE7C2262CE6 = {
    stats(coreStats: string): string {
      let stats = JSON.parse(coreStats)

      stats['bundle_id'] = extractHostname(mainThreadOrigin)
      stats['pspdf_version'] = PSPDFKIT_DEFINES.VERSION
      stats['system_version'] = navigator.userAgent
      stats['preferred_locale'] = navigator.language

      return JSON.stringify(stats)
    },
  }
}
/**
 * Uses self.importScripts in a web worker and require in node
 */
function isomorphicRequire(url: string): Promise<Function> {
  if (
    typeof process === 'object' &&
    // $FlowIssue: We checked for process global object above.
    typeof process.versions === 'object' &&
    // $FlowIssue: We checked for process.versions global object above.
    typeof process.versions.node !== 'undefined'
  ) {
    // In a Node.js environment, we assume that `require()` is available. In this case, the module
    // is exported
    return Promise.resolve(require(url))
  } else if (typeof window === 'object') {
    // In a browser, we have to create a new <script> tag.
    return new Promise((resolve, reject) => {
      let script = document.createElement('script')
      script.type = 'text/javascript'
      script.async = true
      script.onload = () => resolve(window.PSPDFModuleInit)
      script.onerror = reject
      script.src = url

      const { documentElement } = document
      invariant(documentElement)
      documentElement.appendChild(script)
    })
  } else {
    // In a webworker environment, we use `importScripts`. We assume that the module is exported
    // as `self.Module`.
    self.importScripts(url)
    return Promise.resolve(self.PSPDFModuleInit)
  }
}

function extractHostname(url: string): string {
  let hostname
  if (url.indexOf('://') > -1) {
    hostname = url.split('/')[2]
  } else {
    hostname = url.split('/')[0]
  }

  //find & remove port number
  hostname = hostname.split(':')[0]
  //find & remove "?"
  hostname = hostname.split('?')[0]
  //find & remove "#"
  hostname = hostname.split('#')[0]
  //find & remove "/"
  hostname = hostname.split('/')[0]

  return hostname
}
